*::before,*::after{
    box-sizing:border-box;
}

body {
    margin:0;
    background: linear-gradient(to right,#524a5c,#3a3e3c);
    border-radius: 50px;
}

.claculator-container{
    border-radius: 50px;
}

.calculator-grid{
    
    display: grid;
    margin-top: 2rem;
    justify-content:center;
    grid-template-columns: repeat(4,6rem);
    grid-template-rows: minmax(7rem,auto) repeat(5,6rem);
    
}

.span-two,.span-two1{
    grid-column:span 2;
    
}

.output{
    grid-column:1/-1;
    background-color:rgba(43, 35, 35, 0.75);
    display: flex;
    flex-direction:column;
    align-items:flex-end;
    justify-content:space-around;
    padding:.75rem;
    word-break:break-all;
}

.calculator-grid>button
{
    cursor:pointer;
    font-size:2rem;
    border:1px solid white;
    outline:none;
    background-color:rgba(255,255,255,.75);
}

.calculator-grid>button.span-two1{
    grid-column:span 2;
    background-color: grey;
}

.claculator-grid>button:hover,
.calculator-grid>button:focus{
    background-color:rgba(36, 25, 80, 0.9);
    color:white;
}

.output .previous-operand{
    color:rgba(255,255,255,.75);
    font-size: 1.5rem;
}

.output .current-operand{
    color:white;
    font-size:2.5rem;
}